import React, { useState } from "react";
import style from "../ExamDepartment.module.css";
import { useTranslation } from "react-i18next";
import SubjectCreditUpdate from "./SubjectCreditUpdate";
const SubjectCreditCard = ({ sub, onRefetch, isView }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState("");
  return (
    <>
      <div
        className={style.class_container}
        onClick={() => setOpen(sub?._id)}
        style={{
          cursor: "pointer",
        }}
        title="Update subject credit"
      >
        <img
          className={style.oneRowImage}
          src="/images/book-24-icon.svg"
          alt="batch icon"
        />
        <div className={style.class_container_text}>
          <h6>{sub?.subjectName}</h6>
          <p>
            {t(
              isView === "SUBJECT_PASSING"
                ? "course_passing_credit"
                : "course_credit"
            )}{" "}
            {`: `}
            {isView === "SUBJECT_PASSING"
              ? sub?.course_passing_credit ?? 0
              : sub?.course_credit ?? 0}
          </p>
        </div>
      </div>
      {open && (
        <SubjectCreditUpdate
          onClose={() => setOpen("")}
          sid={open}
          onRefetch={onRefetch}
          courseCredit={
            isView === "SUBJECT_PASSING"
              ? sub?.course_passing_credit ?? ""
              : sub?.course_credit ?? ""
          }
          isView={isView}
        />
      )}
    </>
  );
};

export default SubjectCreditCard;
